<template>
	<div class="mine">
		<button @click="back">返回上一页</button>
		<div class="middle">
			{{msg}}
		</div>
	</div>
</template>

<script>
	
	export default {
		name: 'HelloWorld',
		data() {
			return {
				msg: 'Welcome to mine page'
			}
		},
		methods:{
			back(){
				this.$router.back()
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	button{
		margin: 10px;
		border: 1px solid rgba(112,212,12,0.8);
		width: 100px;
		font-size: 14px;
		background: #06BF04;
		border-radius: 10px;
	}
	.middle{
		width: 90%;
		margin: 50% auto;
		text-align: center;
	}
</style>


