<template>
	<div class="header-nav">
		<mu-appbar style="width: 100%;" color="primary">
		  
			<mu-button flat  slot="left">
				保定
		    	<mu-icon value="keyboard_arrow_down" right></mu-icon>
		  	</mu-button>
		  	<mu-text-field  type="text" underline-color="transparent" class="appbar-search-field" style="line-height: 0;" hintText="通话记录" icon="search"></mu-text-field>
		  	<mu-button flat slot="right">
		  		<mu-icon class="saoma_icon" value="crop_free" center></mu-icon>
		  		<span class="saoma">扫码</span>
		  	</mu-button>
		</mu-appbar>
	</div>
</template>

<script>
	import Vue from 'vue';
	import MuseUI from 'muse-ui';
	import 'muse-ui/dist/muse-ui.css';
	Vue.use(MuseUI);
	export default {
		
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.header-nav{
		position: fixed;
	    top: 0px;
	    left: 0px;
	    right: 0px;
	    z-index: 88;
	}
	.mu-primary-color{
		background-color:darkcyan ;
	}
	input:focus{
		outline: transparent;
		border: transparent;
	}
	.appbar-search-field{
		border: 1px solid #999;
		border-radius: 20px;
	}
	.mu-input{
		width: 100%;
		min-height: 0px; 
		margin-bottom: 0px; 
	    padding-top: 0px;
	    padding-bottom:0px;
	    
	}
	.saoma_icon{
		padding-bottom: 1rem;
	}
	.saoma{
		position: absolute;
		margin-top: 0.8rem;
	}
	.mu-input__focus{
		color: #000;
	}
	
</style>


