<template>
	<div class="list-item">
		<mu-paper :z-depth="1" class="demo-loadmore-wrap">
		  <mu-appbar color="teal">
		    <mu-button icon slot="left">
		      <mu-icon value="menu"></mu-icon>
		    </mu-button>
		    LoadMore
		    <mu-button icon slot="right" @click="refresh()">
		      <mu-icon value="refresh"></mu-icon>
		    </mu-button>
		  </mu-appbar>
		  <mu-container ref="container" class="demo-loadmore-content">
		    <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
		      <mu-list>
		        <template v-for="item in lists">
		          <van-card>
		          	<div slot="title">
		          		向下循环开始
		          		<div style="float: right;width: 4rem;text-align: right;">&gt;300m</div>
		          	</div>
		          	<div slot="desc">减价大发送了</div>
		          	<div slot="desc">
		          		<span style="color: forestgreen;">￥59</span>
		          		 <mu-badge content="跳楼价" color="secondary" slot="after"></mu-badge>
		          		 <div style="float: right;width: 4rem;text-align: right;">已售9999</div>
		          	</div>
		          	<div slot="thumb">
					    <img :src="'/static/listImg/'+item.imgurl"/>
					</div>
		          </van-card>
		        </template>
		      </mu-list>
		    </mu-load-more>
		  </mu-container>
		</mu-paper>
	</div>
</template>

<script>
	import Vue from 'vue';
	import axios from 'axios';
	import { Card } from 'vant';
	Vue.use(Card);
	var url ="/static/data.json"
	export default {
		 data () {
		    return {
		      num:10,
		      lists: [],
		      refreshing: false,
		      loading: false,
		      text: 'List'
		    }
		  },
		  mounted  (){
		  	axios.get(url).then(res=>{
		  		this.lists=res.data;
		  	})
		  },
		  methods: {
		    refresh () {
		      this.refreshing = true;
		      this.$refs.container.scrollTop = 0;
		      setTimeout(() => {
		        this.refreshing = false;
		        this.text = this.text === 'List' ? 'Menu' : 'List';
		        this.num = 10;
		      }, 2000)
		    },
		    load () {
		      this.loading = true;
		      setTimeout(() => {
		        this.loading = false;
		        this.num += 10;
		      }, 2000)
		    }
		  }
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.list-item{
		margin-bottom: 56px;
	}
</style>


