<template>
	<div class="banner">
		<van-swipe :autoplay="3000">
		  <van-swipe-item v-for="(image, index) in images" :key="index">
		  	<img v-lazy="image" />
		  </van-swipe-item>
		</van-swipe>
	</div>
</template>

<script>
	import Vue from 'vue';
	
	import { Swipe, SwipeItem ,Lazyload } from 'vant';

	Vue.use(Swipe).use(SwipeItem).use(Lazyload);
	
	export default {
		 data() {
		    return {
		      images: [
		        '/static/1.png',
		        '/static/2.png',
		        '/static/3.png'
		      ]
		    }
		  }
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.banner{
		margin-top:56px;
		transform:translate3d(0,0,0);
		overflow:hidden;
	}
	.banner img{
		display: block;
		width: 100%;
	}
</style>


