import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/pages/Main'
import Home from '@/pages/Home/Home'
import Recent from '@/pages/Recent/Recent'
import Play from '@/pages/Play'
import Order from '@/pages/Order'
import Mine from '@/pages/Mine'
import Meishi from '@/pages/Meishi'

Vue.use(Router)

export default new Router({
  routes: [
  	{
  		path:'/',
  		component:Main,
  		children:[
  			{
		      path: '',
		      name:'/main',
		      component: Home
		    },
  			{
		      path: '/home',
		      name: 'home',
		      component: Home
		    },
		    {
		      path: '/recent',
		      name: 'recent',
		      component: Recent
		    },
		    {
		      path: '/play',
		      name: 'play',
		      component: Play
		    },
		    {
		      path: '/order',
		      name: 'order',
		      component: Order
		    },
		    {
		      path: '/mine',
		      name: 'mine',
		      component: Mine
		    }
  		]
  	},
    {
    	path: '/meishi',
    	name: 'meishi',
    	component:Meishi
    }
  ]
})



