<template>
  <div >
    <router-view></router-view>
    <mu-paper id="footBar">
	  <mu-bottom-nav :value="bottomVal" @change="handleChange">
	    <mu-bottom-nav-item value="/home" title="首页" icon="home"></mu-bottom-nav-item>
	    <mu-bottom-nav-item value="/recent" title="附近" icon="pin_drop"></mu-bottom-nav-item>
	    <mu-bottom-nav-item value="/play" title="逛一逛" icon="language"></mu-bottom-nav-item>
	    <mu-bottom-nav-item value="/order" title="订单" icon="assignment"></mu-bottom-nav-item>
	    <mu-bottom-nav-item value="/mine" title="我的" icon="account_circle"></mu-bottom-nav-item>
	  </mu-bottom-nav>
	</mu-paper>
  </div>
</template>

<script>
	import Vue from 'vue'
	
	import MuseUI from 'muse-ui';
	import 'muse-ui/dist/muse-ui.css';
	Vue.use(MuseUI);
//	import 'muse-ui/lib/styles/base.less';
	import { AppBar,Button,List,Icon,Menu,BottomNav,Paper,TextField} from 'muse-ui';
//	import 'muse-ui/lib/styles/theme.less';
	
	Vue.use(AppBar);
	Vue.use(Button);
	Vue.use(List);
	Vue.use(Icon);
	Vue.use(Menu);
	Vue.use(BottomNav);
	Vue.use(Paper);
	Vue.use(TextField);
	
export default {
  name: 'App',
  data () {
  	return {
  		bottomVal:'/home'
  	}
  },
  methods:{
  	handleChange (val) {
  		this.bottomVal=val;
  		this.$router.push(val)
  	}
  },
  mounted(){
  	let path=this.$route.path;
  	if(path=="/")path="/home"
  	this.bottomVal=path;
  }
}
</script>

<style>
#footBar{
	position: fixed;
	left: 0;
	right: 0;
	bottom: 0;
}
#footBar .mu-bottom-item{
	min-width: 0.62rem;
}
</style>



